// Frontend
// @since NEXT
@import 'mixins';

// General
@import 'frontend/variables',
        'spinner';

// Displaying avatars to choose in "Change Profile Picture"
.avatars-wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: 1rem 0;

    .avatar-wrap {
        margin: .5rem;
        background-color: #ffffff;
        border: 4px solid #d9d9d9;
    }

    .avatar-selected {
        border: 4px solid #21759B;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    }
}

// Fix - Current avatar displaying full size when using BP nouveau template
#item-header-avatar {
    a img {
        max-width: 150px;
    }
}


